<template>
    <div id="sortFlightInternal" class="sorting2">
        <div class="sorting-inner date-change iranL prev">
            <a class="prev-date" v-if="data_search.MultiWay !='TwoWay'"  :href="`${amadeusPathByLang()}international/1/${data_search.origin}-${data_search.destination}/${dataSearch.prev}/Y/${data_search.adult}-${data_search.child}-${data_search.infant}`">
                <i class="zmdi zmdi-chevron-right iconDay"></i>
                <span>{{ useXmltag('Previousday')}}</span>
            </a>
            <a class="prev-date" v-else href="#" v-on:click.prevent.stop="">
                <i class="zmdi zmdi-chevron-right iconDay"></i>
                <span>{{ useXmltag('Previousday')}}</span>
            </a>
        </div>
        <div class="sorting-inner price_sort_color sorting-active-color-main" @click="priceSortFlight()" id="price_sort">
            <span v-html="icon_svg_header_1"></span>
            <span class="text-price-sort iranL">{{ useXmltag('Baseprice')}}</span>
        </div>

        <div class="sorting-inner time_sort_color" @click="timeSortFlight()" id="time_sort">
            <span v-html="icon_svg_header_2"></span>
            <span class="text-price-sort iranL">{{ useXmltag('BaseStartTime')}}</span>

