                <i class="zmdi zmdi-chevron-left iconDay"></i>
            </a>
            <a class="next-date" v-else href="#" v-on:click.prevent.stop="">
                <span>{{ useXmltag('Nextday')}}</span>
                <i class="zmdi zmdi-chevron-left iconDay"></i>
            </a>
        </div>

        <div class="filter_search_mobile_res filter_search_mobile_res_foriegn">
