                data_search: [],
                show_lowest : false,
            }
        },
        components:{
            'lowest-price':lowestPrice
        },
        methods: {
            timeSortFlight() {
                this.$emit('sortByTime')
            },
            priceSortFlight() {
                this.$emit('sortByPrice')
            },
            getLowestPrice(){

                let data_store_lowest = this.$store.state.lowestFlightPrice ;
                document.getElementById('lowest').style.display='block';
                console.log('length ==>' + data_store_lowest.length);
                if(data_store_lowest.length == undefined){
                    this.$store.dispatch('getLowestPrice', {origin: this.dataSearch.dataSearch.origin, destination: this.dataSearch.dataSearch.destination}).then(response => {
                        this.show_lowest = true ;
                        document.getElementById('showDataFlight').style.display='block';
                        document.getElementById('flightFifteen').style.display='none';
                    });
                }else{
                    this.show_lowest = (!this.show_lowest);
                    if(!this.show_lowest){
                        document.getElementById('lowest').style.display='block';
                        document.getElementById('showDataFlight').style.display='block';
                    }else{
                        document.getElementById('lowest').style.display='none';

                    }
                }
            }
        }, watch: {
            dataSearch() {
                let _this = this;
                if (_this.dataSearch) {
                    _this.data_search = _this.dataSearch.dataSearch;
                }
            }
        }
    }
