<template>
    <div class="international-available-airlines-detail-tittle">
                <span class="iranB  lh25 displayb txtRight">
                    <i class="fa fa-circle site-main-text-color "></i>
                    {{ useXmltag('Wentflight')}}
                </span>
        <template v-for="detail_flight in dept_detail_flight">

            <div class="international-available-airlines-detail airlines-stops-time " v-if="detail_flight.is_transit">
                <span class="iranB  lh25 displayib txtRight">
                    <span class=" iranb  lh18 displayib">
                        {{ useXmltag('Stopat')}} :
                    </span>
                    <span class="open  displayib">
                       {{ detail_flight.departure.departure_city}}({{ detail_flight.departure.departure_airport}})
                    </span>
                </span>
                <span class="open  lh25 displayib fltl">
                     {{ detail_flight.transit }}
                </span>

            </div>

            <div class=" international-available-airlines-detail  site-border-right-main-color">
                <div class="international-available-airlines-logo-detail ">
                    <div class="logo-airline-ico-foreign"></div>
                </div>

                <div class="international-available-airlines-info-detail side-logo-detail">
                    <span class="airline_s">
                       <i>{{ useXmltag('Airline')}}</i>
                      {{ detail_flight.airline.airline_name}} <em>-</em>
                    </span>
                    <span class="flightnumber_s">
                     <i> {{ useXmltag('Numflight')}}: </i>
                      {{ detail_flight.flight_number}} <em>-</em>
                    </span>
                    <span class="seatClass_s">
                      {{ detail_flight.seat_class}} <em>-</em>
                    </span>
                    <span class="capacity_s">
                        {{ detail_flight.capacity}} {{ useXmltag('People')}}
                    </span>
                </div>
            </div>

            <div class="international-available-airlines-detail site-border-right-main-color">
                <div class="airlines-detail-box-foreign origin-detail-box">
                    <span class="open  displayb">
                        {{ detail_flight.departure.departure_city}}
                    </span>
                    <span class="open  displayb"> {{ detail_flight.departure.departure_airport}}</span>
                    <span class="openB  displayb">{{ useXmltag('RunTime')}} : ({{ detail_flight.departure_date_abbreviation}})  {{ detail_flight.departure_time}}- {{ detail_flight.departure_date}}</span>
                </div>

                <div class="airlines-detail-box-foreign destination-detail-box">
                    <span class="open  displayb">{{ detail_flight.arrival.arrival_city}}</span>
                    <span class="open  displayb">{{ detail_flight.arrival.arrival_airport}}</span>
                    <span class="openB  displayb" v-if="detail_flight.arrival_date !=null">{{ useXmltag('ArrivingTime')}} :({{ detail_flight.arrival_date_abbreviation}})  {{ detail_flight.arrival_time}}- {{ detail_flight.arrival_date}} </span>
                </div>

                <div class="airlines-detail-box details-detail-box ">
                                    <span class="w-100">
                                            {{ useXmltag('Permissibleamount')}} : <i class="iranNum">{{ detail_flight.baggage.baggage_statement}}</i>
                                    </span>

                    <span class="w-100" v-if="detail_flight.cabin_type !=''">  {{ useXmltag('Classrate')}}:<i class="openL">{{ detail_flight.cabin_type}} </i> </span>

                </div>
            </div>
        </template>
    </div>
</template>

<script>
    export default {
        name: "detailDepartFlight",
        props: ['dept_detail_flight'],
    }
</script>

<style scoped>

</style>