<template>
    <div class="international-available-item-left-Cell my-slideup">
        <div class="inner-avlbl-itm ">
            <div class="">
        <span class="iranL priceSortAdt">
            <template v-if="each_price_flight.price.adult.has_discount === 'yes'">
                <i class="iranB text-decoration-line displayb CurrencyCal"
                   :data-amount="price_adult" v-if="checkCurrency">
                    {{ price_adult | formatNumberDecimal}}
                </i>
                <i class="iranB text-decoration-line  site-main-text-color-drck CurrencyCal"
                        :data-amount="price_adult" v-else>
                    {{ price_adult | formatNumber}}
                </i>
                <i class="iranB site-main-text-color-drck CurrencyCal"
                   :data-amount="each_price_flight.price.adult.with_discount" v-if="checkCurrency">
                    {{ price_adult_with_discount  | formatNumberDecimal}}
                </i>
                   <i class="iranB site-main-text-color-drck CurrencyCal"
                      :data-amount="each_price_flight.price.adult.with_discount" v-else>
                    {{ price_adult_with_discount  | formatNumber}}
                </i>
                <span class="CurrencyText">
                      {{title_currency }}
                </span>
                </template>
                <template v-else>
                <i class="iranB site-main-text-color-drck CurrencyCal"
                   :data-amount="price_adult" v-if="checkCurrency">
                    {{ price_adult | formatNumberDecimal}}
                </i>
                   <i class="iranB site-main-text-color-drck CurrencyCal"
                      :data-amount="price_adult" v-else>
                    {{ price_adult | formatNumber}}
                </i>
                <span class="CurrencyText">
                      {{title_currency }}
                </span>
                </template>


                </span>
                <div class="SelectTicket">
                    <a class="international-available-btn site-bg-main-color site-main-button-color-hover nextStepclass"
                       :id="`nextStep_${each_price_flight.flight_id.replace('#','')}`"
                       v-if="each_price_flight.return_route !=''">
                        {{ useXmltag('Selectionflight')}}
                    </a>
                    <a class="international-available-btn site-bg-main-color site-main-button-color-hover nextStepclass"
                       :id="`nextStep_${each_price_flight.flight_id.replace('#','')}${each_price_flight.return_routes.flight_id_return.replace('#','')}`"
                       v-else>
                        {{ useXmltag('Selectionflight')}}
                    </a>
                    <input type="hidden" value="" name="session_filght_Id" id="session_filght_Id">
                    <span class="f-loader-check f-loader-check-change"
                          :id="`loader_check_${each_price_flight.flight_id.replace('#','')}`"
                          style="display:none"></span>

                    <input type="hidden" value="" class="CurrencyCode">
                    <input type="hidden" :value="each_price_flight.flight_id" class="FlightID">
                    <input type="hidden" :value="each_price_flight.return_routes.return_flight_id"
                           class="ReturnFlightID">
                    <input type="hidden" :value="price_adult" class="AdtPrice">
                    <input type="hidden" :value="each_price_flight.price.child.price" class="ChdPrice">
                    <input type="hidden" :value="each_price_flight.price.infant.price" class="InfPrice">
                    <input type="hidden" :value="each_price_flight.cabin_type" class="CabinType">
                    <input type="hidden" :value="each_price_flight.airline" id="Airline_Code" class="Airline_Code">
                    <input type="hidden" :value="each_price_flight.source_id" id="SourceId" class="SourceId">
                    <input type="hidden" :value="each_price_flight.flight_type_li" id="FlightType" class="FlightType">
                    <input type="hidden" :value="each_price_flight.unique_code" id="uniqueCode" class="uniqueCode">
                    <input type="hidden" :value="each_price_flight.capacity" id="Capacity" class="priceWithoutDiscount">
                    <input type="hidden" value="dept" class="FlightDirection">


                </div>
            </div>
        </div>
    </div>
</template>

<script>

    export default {
        name: "priceFlight",
        props: ['each_price_flight'],
        data() {
            return {
                check_currency_data: true
            }
        },

        computed: {
            checkCurrency() {
                let info_price = this.$store.state.priceCurrency;
                if (this.check_currency_data) {
                    return (Object.keys(info_price).length > 1 || (this.each_price_flight.currency_code > 0));
                }
                return this.check_currency_data;

            },
            price_adult() {
                let info_price = this.$store.state.priceCurrency;
                let price = this.each_price_flight.price.adult.price;
                let price_with_out_currency = this.each_price_flight.price.adult.price_with_out_currency;
                if(Object.keys(info_price).length !==0)
                {
                    if (Object.keys(info_price.data).length > 1) {
                        if (info_price.data.CurrencyCode > 0) {
                            this.check_currency_data = true;
                            return (price_with_out_currency / info_price.data.EqAmount).toFixed(2);
                        }
                    } else if (Object.keys(info_price.data).length === 0) {
                        this.check_currency_data = false;
                        return price_with_out_currency;
                    }
                }

                return price;
            },
            price_adult_with_discount() {
                let info_price = this.$store.state.priceCurrency;
                let price = this.each_price_flight.price.adult.with_discount;
                let price_discount_with_out_currency = this.each_price_flight.price.adult.price_discount_with_out_currency;
                if(Object.keys(info_price).length !==0) {
                    if (Object.keys(info_price.data).length > 1) {
                        if (info_price.data.CurrencyCode > 0) {
                            this.check_currency_data = true;
                            return (price_discount_with_out_currency / info_price.data.EqAmount).toFixed(2);
                        }
                    } else if (Object.keys(info_price.data).length === 0) {
                        this.check_currency_data = false;
                        return price_discount_with_out_currency;
                    }
                }
                return price;
            },
            title_currency() {
                let info_price = this.$store.state.priceCurrency;
                let type_currency = this.each_price_flight.price.adult.type_currency;
                if (Object.keys(info_price).length !==0) {
                    if (Object.keys(info_price.data).length > 0) {
                        if (info_price.data.CurrencyCode > 0) {
                            return info_price.data.CurrencyTitleEn;
                        }
                    } else if (Object.keys(info_price.data).length === 0) {

                        return useXmltag('Rial');
                    }
                }
                return type_currency;
            }
        }
    }
</script>

