                                <img :src="`${getUrlWithoutLang()}/pic/flagCurrency/${currency.CurrencyFlag}`"
                                     alt="">
                                <span>{{currency.CurrencyTitle}}</span>
                            </div>
                        </template>
                        <!---->
                    </div>
                </div>
            </div>
