                                    <label class=" site-bg-main-color-before" for="huey">
                                        <i v-html="svg_icon_2"></i>
                                        {{ useXmltag('Oneway') }}
                                    </label>
                                </div>
                                <div @click="changeWays_('Twoway')" class="radiobtn Twoway  ">
                                    <input type="radio" id="dewey" name="drone" value="dewey"
