                                           :class="(multi_way) ? 'checked' : ''" class="multiWays">
                                    <label class="site-bg-main-color-before" for="dewey">
                                        <i v-html="svg_icon_1"></i>
                                        {{ useXmltag('Twoway') }}

                                    </label>
                                </div>
