                                        <option :selected="city['Departure_Code'] == data_search.origin" v-for="city in cities_origin"  :value="city['Departure_Code']" >
                                            <template v-if ="data_search.software_lang !=='fa'">
                                            {{ city[`Departure_City${getLang().charAt(0).toUpperCase() + getLang().slice(1)}`] }}({{city['Departure_Code']}})
                                            </template>
                                            <template v-else>
                                                {{ city[`Departure_City`] }}({{city['Departure_Code']}})
                                            </template>

                                        </option>
                                    </select>
                                </div>
