                                            <template v-if ="data_search.software_lang !=='fa'">
                                                {{ city_arrival[`Arrival_City${getLang().charAt(0).toUpperCase() + getLang().slice(1)}`] }}({{city_arrival['Arrival_Code']}})
                                            </template>
                                            <template v-else>
                                                {{ city_arrival[`Arrival_City`] }}({{city_arrival['Arrival_Code']}})
                                            </template>
                                        </option>

                                    </select>
                                </div>
                            </div>
                        </div>

