                                                          v-model="data_search.departureDate" element="dept_date_foreign"
                                                          :min="dateNow('-')" :format="format_datepicker"  popover="bottom-right"/>
                                        </template>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="s-u-form-block s-u-num-inp s-u-num-inp-change width100"
